import React from 'react'
import main from '../style/main'

export const Home = () => {
    return (
        <div style={{flexGrow: 1, display: "flex", justifyContent: "center", paddingRight: "15%"}}>
            <div style = {main.center}>
                <div style = {main.title}>
                    Angela Lam
                </div>
            </div>
        </div>
            
    );
}

export default Home