import React from 'react'
import main from '../style/main'
import colors from '../style/colors'
import dragon from '../icons/dragon.svg'

const Hobbies = () => {
    return (
            <div style = {{flexGrow: 1, justifyContent: "left", textAlign: "left", alignItems: "left", paddingLeft: "3em", paddingTop: "3em", paddingRight: "8em"}}>
                <div style = {Object.assign({}, main.title, {color: colors.blue})}> Hobbies </div>
                <div style = {main.subtitle}> Hi! Welcome to my Hobbies Page! </div>
                <div style = {{fontSize: ".8em"}}> 
                         My goal with this page is to make it a resource for my friends and myself to keep track or record what is going on in our games.
                         I look forward to seeing how this page grows, and hope you will as well!
                </div>

                <div style = {Object.assign({}, main.row, {marginTop: "2em", justifyContent: "space-between"})}>
                        <div>
                            <div style = {{display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "10em", height: "10em", borderRadius: "5%", background: colors.dragon}}>
                                <img src={dragon} alt = ""/>
                                <div>D&amp;D</div>
                            </div>
                        </div>
                        <div>
                            <div style = {{display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "10em", height: "10em", borderRadius: "5%", background: colors.dragon}}>
                                <img src={dragon} alt = ""/>
                                <div>Art</div>
                            </div>
                        </div> 
                        <div>
                            <div style = {{display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "10em", height: "10em", borderRadius: "5%", background: colors.dragon}}>
                                <img src={dragon} alt = ""/>
                                <div>Archery</div>
                            </div>
                        </div>
                    </div>  
            </div>
    );
}

export default Hobbies