export const main = {
    appLayout: {
        background: "white",
        minHeight: "100vh",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        fontSize: "calc(10px + 2vmin)",
        color: "#282c34",
    },

    center:{
        display: "flex",
        alignItems: "center",
        justifyContent: "center,"
    },
    
    row: {
        display: "flex",
        flexDirection: "row",
        justifyContent:"space-around",
        width: "100%",
    },

    title: {
        fontSize: "2em",
        marginRight:"10px",
        marginBottom: "1em"
    },

    subtitle: {
        fontSize: "1.2em",
        marginBottom: "1em"
    }
}

export default main